.btn-primario {
  background-color: #84d9bc;
  border: none;
  cursor: pointer;
  color: #313c4e;
  display: inline-block;
  font-weight: 700;
  font-size: 2rem;
  font-weight: 400;
  margin-top: 2rem;
  padding: 1rem 3rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  font-weight: 700;
}
.btn-primario:hover {
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .btn-primario {
    width: unset;
  }
}